<template>
  <div class="manage_user">
    <listSearch v-model="searchFormData" :searchArr="searchArr"></listSearch>
    <div class="content">
      <div class="buttons">
        <Button type="primary" @click="showPopup">添加</Button>
      </div>
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="id">
            <span>{{ row.id }}</span>
          </template>
          <template slot-scope="{ row }" slot="username">
            <span>{{ row.username }}</span>
          </template>
          <template slot-scope="{ row }" slot="nickname">
            <span>{{ row.nickname }}</span>
          </template>
          <template slot-scope="{ row }" slot="mobile">
            <span>{{ row.mobile }}</span>
          </template>
          <template slot-scope="{ row }" slot="sex">
            <span>{{ row.sex }}</span>
          </template>
          <template slot-scope="{ row }" slot="createTime">
            <span>{{ row.createTime }}</span>
          </template>
          <template slot-scope="{ row }" slot="enabled">
            <span>{{ row.enabled }}</span>
          </template>
          <template slot="action">
            <div>
              <Button type="primary">同意</Button>
              <Button type="primary">驳回</Button>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20" :total="pageObj.count" 
          show-elevator
          show-sizer
          @on-page-size-change="pageSizeChange"
          @on-change="pageNumChange"/>
      </div>
    </div>
    <!-- 弹窗 -->
    <popupBox :show="popupShow" 
      @popupConfirm="popupConfirm"
      @popupCancel="popupCancel">
      <div class="my_content">123</div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import popupBox from '@/components/popup/popup-box'
import mixin from './mixin'
export default {
  name: 'manage_user',
  components: { listSearch, popupBox },
  mixins: [mixin],
  data () {
    return {
      popupShow: false,
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      searchArr: [
        {type: 'input', value: 'personalName', label: '姓名', placeholder: '请输入个人姓名'},
        {type: 'input', value: 'companyName', label: 'ID', placeholder: '请输入ID'},
      ],
      columns: [
        {
          title: '编号',
          slot: 'id'
        },
        {
          title: '账号',
          slot: 'username'
        },
        {
          title: '用户名',
          slot: 'nickname'
        },
        {
          title: '手机号',
          slot: 'mobile'
        },
        {
          title: '性别',
          slot: 'sex'
        },
        {
          title: '创建时间',
          slot: 'createTime'
        },
        {
          title: '状态',
          slot: 'enabled'
        },
        {
          title: '操作',
          slot: 'action'
        }
      ],
      bodyList: []
    }
  },
  watch: {
    // searchFormData (newVal) {
    //   console.log(newVal)
    // }
  },
  mounted () {
    this.getList()
  },
  methods: {
    // 按钮点击添加
    showPopup () {

    },
    // 弹窗确定
    popupConfirm () {
      console.log(1)
    },
    // 请求用列表
    getList () {
      let params = {
        page: this.pageObj.page,
        limit: this.pageObj.limit
      }
      this.manageApi.getUsersList(params)
        .then(res => {
          console.log(res)
          this.bodyList = res.data
        })
        .catch(err => {
          console.log(err)
          this.$Message.error({
            content: err,
            duration: 5 // 持续几秒
          })
        })
    }
  }
}
</script>
<style lang="less">
@import url('../../style/iview_table.less');
</style>
<style lang="less" scoped>
.manage_user {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.my_content {
  color: red
}
</style>