export default {
  data() {
    return {
      pageObj: {
        page: 1,
        limit: 10,
        count: null
      }
    }
  },
  methods: {
    // 修改每页数量
    pageSizeChange (num) {
      this.pageObj.limit = num
    },
    // 翻页
    pageNumChange (num) {
      this.pageObj.page = num
    },
    // 显示弹窗
    showPopup () {
      this.popupShow = true
    },
    // 弹窗取消
    popupCancel () {
      this.popupShow = false
    },
  }
  
}